.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


#canvas-container {
  width: 100vw;
  height: 100vh;
}
.debug {
  width: 16rem;
  font-size: 0.875rem;
  overflow: auto;
  padding: 0.5rem;
  border-radius: 0.125rem;
  position: fixed;
  bottom: 0.5rem;
  left: 0.5rem;
  color: white;
  background: #00000020;
}
.debug pre {
  margin: 0;
}
.crosshair {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 2rem;
}